import uniTransition from '@/components/uni-transition/uni-transition.vue';
import viewHighlight from '@/components/view-highlight.vue';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import cmdIcon from '@/components/cmd-icon/cmd-icon.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';

import { mapState } from 'vuex';

export default {
	components: { uniTransition, viewHighlight, uniSegmentedControl, cmdIcon, uniPopup },
	name: 'icon-picker',
	props: {
		tabItems: {
			type: Array
		},
		iconSets: {
			type: Array
		},
		defIcon: {
			type: String,
			default: 'transparent'
		},
		w: {
			type: Number,
			default: 50
		},
		h: {
			type: Number,
			default: 50
		},
		viewH: {
			type: Number,
			default: 600
		}
	},
	data() {
		return {
			e: [],
			maskClick: false,
			showColorPicker: false,
			icon: '',
			style: '',
			hl: false,
			tabIndex: 0,
			transfromClass: {
				position: 'absolute',
				bottom: 0,
				top: 0,
				left: 0,
				right: 0,
				width: '100%',
				margin: 'auto',
				display: 'flex',
				'justify-content': 'center',
				'background-color': 'black'
			}
		};
	},
	methods: {
		getIcon(icon) {
			// this.e = e;
			// console.log(JSON.stringify(icon));
			this.icon = icon;
			console.log(this.icon);
		},
		onChangeColorSetTabItem: function(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		},
		transIsDone() {},
		onDone() {},
		submit() {
			this.$refs.popupIcon.close();
			this.$emit('loadIconPicker', this.icon);
		},
		openPopup() {
			this.$refs.popupIcon.open();
		}
	},
	mounted() {
		this.icon = this.defIcon;
		console.log('icon-picker.onLoad' + this.icon);
	}
};
